'use client';

import React, { createContext, useContext, useState, useEffect } from 'react';

// Types for our distinct user principals
interface KomoraUser {
    id: string;
    email: string;
    firstName: string;
    profileType: 'PARTICULIER' | 'COMMERCANT' | 'RESTAURATEUR' | 'LIVREUR' | 'FOURNISSEUR';
}

interface CandidateUser {
    id: string;
    email: string;
    firstName: string;
    lastName: string;
}

interface AuthContextType {
    // KomoraLink State (Docs, Dashboard access)
    komoraUser: KomoraUser | null;
    isKomoraLoading: boolean;
    loginKomora: (token: string, user: KomoraUser) => void;
    logoutKomora: () => void;

    // Careers State (Job Applications)
    candidateUser: CandidateUser | null;
    isCandidateLoading: boolean;
    loginCandidate: (token: string, user: CandidateUser) => void;
    logoutCandidate: () => void;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export function AuthProvider({ children }: { children: React.ReactNode }) {
    // --- KomoraLink Auth State ---
    const [komoraUser, setKomoraUser] = useState<KomoraUser | null>(null);
    const [isKomoraLoading, setIsKomoraLoading] = useState(true);

    // --- Candidate Auth State ---
    const [candidateUser, setCandidateUser] = useState<CandidateUser | null>(null);
    const [isCandidateLoading, setIsCandidateLoading] = useState(true);

    // Load state from storage on mount
    useEffect(() => {
        // Load Komora User
        const kToken = localStorage.getItem('komora_access_token');
        const kUserStr = localStorage.getItem('komora_user_data');
        if (kToken && kUserStr) {
            try {
                setKomoraUser(JSON.parse(kUserStr));
            } catch (e) {
                console.error("Failed to parse Komora user data", e);
                localStorage.removeItem('komora_access_token');
                localStorage.removeItem('komora_user_data');
            }
        }
        setIsKomoraLoading(false);

        // Load Candidate User
        const cToken = localStorage.getItem('candidate_access_token');
        const cUserStr = localStorage.getItem('candidate_user_data');
        if (cToken && cUserStr) {
            try {
                setCandidateUser(JSON.parse(cUserStr));
            } catch (e) {
                console.error("Failed to parse Candidate user data", e);
                localStorage.removeItem('candidate_access_token');
                localStorage.removeItem('candidate_user_data');
            }
        }
        setIsCandidateLoading(false);
    }, []);

    // --- Komora Actions ---
    const loginKomora = (token: string, user: KomoraUser) => {
        localStorage.setItem('komora_access_token', token);
        localStorage.setItem('komora_user_data', JSON.stringify(user));
        setKomoraUser(user);
    };

    const logoutKomora = () => {
        localStorage.removeItem('komora_access_token');
        localStorage.removeItem('komora_user_data');
        setKomoraUser(null);
    };

    // --- Candidate Actions ---
    const loginCandidate = (token: string, user: CandidateUser) => {
        localStorage.setItem('candidate_access_token', token);
        localStorage.setItem('candidate_user_data', JSON.stringify(user));
        setCandidateUser(user);
    };

    const logoutCandidate = () => {
        localStorage.removeItem('candidate_access_token');
        localStorage.removeItem('candidate_user_data');
        setCandidateUser(null);
    };

    return (
        <AuthContext.Provider value={{
            komoraUser,
            isKomoraLoading,
            loginKomora,
            logoutKomora,
            candidateUser,
            isCandidateLoading,
            loginCandidate,
            logoutCandidate
        }}>
            {children}
        </AuthContext.Provider>
    );
}

export function useAuth() {
    const context = useContext(AuthContext);
    if (context === undefined) {
        throw new Error('useAuth must be used within an AuthProvider');
    }
    return context;
}
